body {
	background-color: #f0f0f0;
}

.title {
	text-align: center;
}
.regles{
	width:20%;
	height: 500px;
	color: black;
	display: inline-block;
	vertical-align:top;
	margin-left:20px;
		display: none;
}
#big {
	margin-left:5%;
	.contain {
		.CodeMirror {
			height: 700px;
		}
		.ReactCodeMirror {
			min-height: 800px;
			vertical-align: top;
			margin-bottom:15px;
			display:inline-block;
			width: 49%;
		}
	}
	.resultat {
		height: 700px;
		width: 49%;
		overflow-y: scroll;
		padding-left: 5px;
		margin-left: 5px;
		display:inline-block;
		color:black;
		background-color: white;
		padding-left: 5px;
	}
}
::-webkit-scrollbar {
    width: 12px;
}
 
::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3); 
    border-radius: 10px;
}
 
::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5); 
}